const SystemInfo = uni.getSystemInfoSync();

export default {
	data() {
		return {
			imageList: [{
					src: '../../static/boot/guide_1.9.png'
				},
				{
					src: '../../static/boot/guide_2.9.png'
				},
				{
					src: '../../static/boot/guide_3.9.png'
				},
				{
					src: '../../static/boot/guide_4.9.png'
				}
			],
			autoPlay: false,
			currIndex: 0,
			screenWidth: SystemInfo.screenWidth
		};
	},
	methods: {
		sliderChange(e) {
			console.log(e);
			this.currIndex = e.detail.current;
		},
		launchAppIndex() {
			console.log('launchAppIndex');
			if (this.imageList.length == this.currIndex + 1) {
				this.launchApp();
			} else {
				return;
			}
		},
		launchApp() {
			// 直接到APP 且下次不再显示引导页
			uni.setStorage({
				key: 'launchFlag',
				data: true,
				success() {
					uni.redirectTo({
						url: '/pages/login/login'
					});
				}
			});
		}
	}
};
